<template>
    <div class="address">
        <ul class="address-ul">
            <li class="address-li" v-for="(item,index) in list" :key="index">
                <div class="address-radio" v-show="false" :class="{active:item.id == value}" @click="selectAddress(item,index)"></div>
                <div class="address-main">
                    <div class="add-li-a" @click="selectAddress(item,index)">
                        <div class="address-name">张三</div>
                        <div class="address-tel">15667766555</div>
                        <div class="address-tag" v-show="item.isDefault">{{defalutTagText}}</div>
                    </div>
                    <div class="add-li-b" @click="selectAddress(item,index)">南京市玄武区乱营西路1号</div>
                    <div class="add-li-c">
                        <div class="add-li-cleft">
                            <div class="add-setdefault" @click="setDefaultAddress(item,index)">
                                <span class="address-icon" :class="{active:item.isDefault}"></span>
                                <span class="address-deftxt">设为默认</span>
                            </div>

                        </div>
                        <div class="add-li-crig">
                            <div class="add-li-opera" @click="editAddress(item,index)">
                                <span class="address-icon-edit"></span>
                                <span class="address-deftxt">编辑</span>
                            </div>
                            <div class="add-li-opera" @click="deleteAddress(item,index)">
                                <span class="address-icon-delete"></span>
                                <span class="address-deftxt">删除</span>
                            </div>
                        </div>
                    </div>
                </div>

            </li>
        </ul>
        <div class="add-address-btn" @click="addAddress">{{addressButtonText}}</div>
    </div>
</template>
<script>
    export default {
        name: 'addressList',
        props:{
            list:{
                type:Array,
                default:[]
            },
            defalutTagText:{
                type:String,
                default:'默认'
            },
            value:{
                type:[Array,String],
                default:null
            },
            addressButtonText:{
                type:String,
                default:'新增地址'
            }
        },
        computed:{

        },
        watch:{

        },
        data() {
            return {

            }
        },
        mounted(){
            //console.log('this.value');
            //console.log(this.value);
            //console.log(this.list);
        },
        methods:{
            //新增
            addAddress(){
                this.$emit('add');
            },
            //编辑
            editAddress(item,index){
                this.$emit('edit',item,index);
            },
            //删除
            deleteAddress(item,index){
                this.$emit('delete',item,index);
            },
            //选择地址点击
            selectAddress(item,index){
                this.$emit('input',item.id);
                this.$emit('select',item,index);
            },
            //设为默认地址点击
            setDefaultAddress(item,index){
                this.$emit('defaultAddress',item,index);
            }
        }
    }
</script>
<style scoped>
    .address{overflow: hidden;}
    .address-ul{display: block;overflow: hidden;}
    .address-li{display: block;overflow: hidden;background: #fff;border-radius: 8px;border:1px solid #ccc;
        margin-bottom: 10px;
    }
    .address-name{height: 40px;width: auto;float:left;font-size: 14px;color:#333;font-weight: 600;
        margin-left:10px;
    }
    .address-tel{height: 40px;width: auto;float:left;font-size: 14px;color:#333;font-weight: 600;margin-left:10px;}
    .add-li-a{overflow: hidden;line-height: 40px;}
    .add-li-b{overflow: hidden;line-height: 18px;color:#333;font-size: 14px;margin-left:10px;height: auto;margin-bottom: 5px;}
    .add-li-c{height: 40px;border-top:1px solid #f2f2f2;overflow: hidden;}
    .add-li-cleft{width: auto;float:left;overflow: hidden;margin-left:10px;line-height: 40px;}
    .add-li-crig{width: auto;float:right;overflow: hidden;line-height: 40px;

    }
    .address-icon{display: block;width: 16px;height: 16px;float:left;margin-top:12px;
        background: url("../../assets/images/check1.jpg") no-repeat center;background-size: 100%;
    }
    .address-icon.active{background: url("../../assets/images/check1_active.png") no-repeat center;background-size: 100%;}
    .address-icon-edit{display: block;width: 16px;height: 16px;float:left;margin-top:12px;
        background: url("../../assets/images/icon-edit.png") no-repeat center;background-size: 100%;
    }
    .address-icon-delete{
        display: block;width: 16px;height: 16px;float:left;margin-top:12px;
        background: url("../../assets/images/icon-delete.png") no-repeat center;background-size: 100%;
    }
    .address-deftxt{display: block;float:left;font-size: 14px;margin-left:5px;}
    .add-li-opera{cursor:pointer;margin-right: 10px;overflow: hidden;float:left;}
    .address-tag{float:left;width: auto;height: 16px;background: #ee0a24;color:#fff;font-size: 12px;line-height: 16px;
        padding-left:3px;padding-right: 3px;border-radius: 4px;overflow: hidden;margin-top:11px;margin-left:5px;
    }
    .add-setdefault{width: auto;float:left;overflow: hidden;cursor:pointer;}
    .add-address-btn{height: 40px;line-height: 40px;text-align: center;background:#ee0a24;color:#fff;font-size: 14px;
        border-radius: 20px;cursor:pointer;
    }
    .address-radio{width: 20px;height: 20px;float:left;overflow: hidden;
        background: url("../../assets/images/check1.jpg") no-repeat center;background-size: 100%;margin:40px 10px;
        margin-right: 0px;cursor:pointer;
    }
    .address-radio.active{background: url("../../assets/images/check1_active.png") no-repeat center;background-size: 100%;}
    .address-main{overflow: hidden;margin-left:10px;}
</style>


