<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">AddressList 地址列表</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                展示收货地址列表。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">
                <div style="width: 400px;">
                    <AddressList :list="list"
                                 :defalutTagText="'默认'"
                                 v-model="chosenAddressId"
                                 :addressButtonText="addressButtonText"
                                 @add="addAddress"
                                 @edit="editAddress"
                                 @delete="deleteAddress"
                                 @select="select"
                                 @defaultAddress="defaultAddress"
                    ></AddressList>
                </div>

            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code>
                                &lt;AddressList :list="list" :defalutTagText="'默认'"
                                             v-model="chosenAddressId"
                                             @add="addAddress"
                                             @edit="editAddress"
                                             @delete="deleteAddress"
                                &gt;
                                &lt;/AddressList&gt;

                                export default {
                                    data() {
                                        return {
                                            chosenAddressId:'1',
                                            list:[
                                                {
                                                    id: '1',
                                                    name: '张三',
                                                    tel: '13000000000',
                                                    address: '浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室',
                                                    isDefault: true,
                                                },
                                                {
                                                    id: '2',
                                                    name: '李四',
                                                    tel: '1310000000',
                                                    address: '浙江省杭州市拱墅区莫干山路 50 号',
                                                },
                                            ]
                                        }
                                    },
                                    methods:{
                                        //新增地址回调
                                        addAddress() {
                                            alert('新增地址');
                                        },
                                        //编辑地址
                                        editAddress(item,index) {
                                            alert('编辑地址');
                                        },
                                        //删除地址
                                        deleteAddress(item,index) {
                                            alert('删除地址');
                                        },
                                        //选中回调
                                        select(item,index){
                                            console.log(item);
                                        },
                                        //默认地址点击
                                        defaultAddress(item,index){
                                            alert('设为默认地址');
                                        }
                                    },
                                }
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">属性</div>
            <div class="hb-a">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="param"
                            label="参数"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                    <el-table-column
                            prop="defaultVal"
                            label="默认值">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">Events</div>
            <div class="hb-a">
                <el-table
                        :data="tableDataEvent"
                        style="width: 100%">
                    <el-table-column
                            prop="eventName"
                            label="事件名"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="backParams"
                            label="回调参数">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">Address 数据结构</div>
            <div class="hb-a">
                <el-table
                        :data="tableDataAddress"
                        style="width: 100%">
                    <el-table-column
                            prop="key"
                            label="键名"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                </el-table>
            </div>
        </div>

    </div>
</template>

<script>
    import AddressList from '@/components/template/AddressList.vue'

    export default {
        name: 'wladdresslist',
        data() {
            return {
                chosenAddressId: '1',
                addressButtonText: '新增地址',
                list: [
                    {
                        id: '1',
                        name: '张三',
                        tel: '13000000000',
                        address: '浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室',
                        isDefault: true,
                    },
                    {
                        id: '2',
                        name: '李四',
                        tel: '1310000000',
                        address: '浙江省杭州市拱墅区莫干山路 50 号',
                    },
                ],
                tableData: [
                    {
                        param: 'v-model',
                        desc: '当前选中地址的 id',
                        type: 'string',
                        defaultVal: '',
                    },
                    {
                        param: 'list',
                        desc: '地址列表',
                        type: 'Array',
                        defaultVal: '[]',
                    },
                    {
                        param: 'defalutTagText',
                        desc: '默认地址标签文字',
                        type: 'string',
                        defaultVal: '默认',
                    },
                    {
                        param: 'addressButtonText',
                        desc: '底部按钮文字',
                        type: 'string',
                        defaultVal: '新增地址',
                    },
                ],
                tableDataEvent: [
                    {
                        eventName: 'add',
                        desc: '点击新增按钮时触发',
                        backParams: '',
                    },
                    {
                        eventName: 'edit',
                        desc: '点击编辑按钮时触发',
                        backParams: 'item: 地址对象，index: 索引',
                    },
                    {
                        eventName: 'delete',
                        desc: '点击删除按钮时触发',
                        backParams: 'item: 地址对象，index: 索引',
                    },
                    {
                        eventName: 'select',
                        desc: '切换选中的地址时触发',
                        backParams: 'item: 地址对象，index: 索引',
                    }
                ],
                tableDataAddress:[
                    {
                        key:'id',
                        desc:'每条地址的唯一标识',
                        type:'number | string'
                    },
                    {
                        key:'name',
                        desc:'收货人姓名',
                        type:'string'
                    },
                    {
                        key:'tel',
                        desc:'收货人手机号',
                        type:'number | string'
                    },
                    {
                        key:'address',
                        desc:'收货地址',
                        type:'string'
                    },
                    {
                        key:'isDefault',
                        desc:'是否为默认地址',
                        type:'boolean'
                    }
                ]
            }
        },
        mounted() {
            this.doCode();
        },
        methods: {
            //一进来需要执行的
            doCode() {
                $('.view-code-btn').click(function () {
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
            //新增地址回调
            addAddress() {
                alert('新增地址');
            },
            //编辑地址
            editAddress(item,index) {
                alert('编辑地址');
            },
            //删除地址
            deleteAddress(item,index) {
                alert('删除地址');
            },
            //选中回调
            select(item,index){
                console.log(item);
            },
            //默认地址点击
            defaultAddress(item,index){
                alert('设为默认地址');
            }
        },
        components: {
            AddressList
        }
    }
</script>
<style scoped>

</style>



